const initState = {
    error: '',
    flag: false
};
const rankDetailsStore = (state = initState, action = {}) => {
    switch (action.type) {
        case 'rankDetailsList':
            return {
                ...action.data,
                flag: true
            };
        case 'rankDetailsEmpty':
            return initState;
        case 'rankDetailsError':
            return {error: '404'};
        default:
            return state
    }
};

export default rankDetailsStore
