import React, {Component} from 'react';
import classnames from "classnames";
import './SmallPlayer.styl';
import PropTypes from 'prop-types';

import prePlayButton from "../../../common/image/prePlay.svg";
import stopButton from "../../../common/image/stop.svg";
import playButton from "../../../common/image/play.svg";
import nextPlayButton from "../../../common/image/nextPlay.svg";
import listButton from "../../../common/image/list.svg";
import musicLogo from "../../../common/image/music.svg";

//引入组件
class SmallPlayer extends Component {
    constructor(props){
        super(props)
        this.state={
            imgSrc:musicLogo
        }
    }
    static propTypes={
        smallPlayerToLargePlayerAction:PropTypes.func.isRequired,
        playChangeAction:PropTypes.func.isRequired,
        playerStore:PropTypes.object.isRequired,
        switchSong:PropTypes.func.isRequired,
        playListDisplayAction:PropTypes.func.isRequired,
    };
    smallPlayerToLargePlayer = (e) => {
        e.stopPropagation();
        this.props.smallPlayerToLargePlayerAction()
    };
    playChange=(e,data)=>{
        e.stopPropagation();
        this.props.playChangeAction(data)
    };
    switchSong=(e,data)=>{
        e.stopPropagation();
        this.props.switchSong(data)
    };
    playListDisplay=(e)=>{
        e.stopPropagation();
        this.props.playListDisplayAction()
    };
    imgOnLoad=(e,picUrl)=>{
        this.setState({
            imgSrc:picUrl
        })
    }
    render() {
        const {playerStore} = this.props;
        return (
            <div onClick={this.smallPlayerToLargePlayer} className={classnames('small_player_container', {'player_display': !playerStore.smallPlayer})}>
                <div className='small_player_img'>
                    {playerStore.songList.length===0?
                        <img className='img_width4' src={musicLogo} alt=""/>
                        :
                        <img className='img_width4' src={this.state.imgSrc} alt=""/>
                    }
                    <img style={{display:'none'}} onLoad={(e)=>this.imgOnLoad(e,playerStore.songList[playerStore.currentPlaySong].picUrl)} src={playerStore.songList[playerStore.currentPlaySong].picUrl} alt=""/>
                </div>
                <div className='small_player_tools'>
                    <img
                        className='img_width2'
                        src={prePlayButton}
                        alt=""
                        onClick={(e)=>{this.switchSong(e,'pre')}}
                    />
                    {playerStore.playing ?
                        <img
                            onClick={(e) => this.playChange(e, 'pause')}
                            className='img_width2_5'
                            src={stopButton}
                            alt=""
                        />
                        : <img
                            onClick={(e) => this.playChange(e, 'play')}
                            className='img_width2_5'
                            src={playButton}
                            alt=""
                        />
                    }
                    <img
                        onClick={(e)=>{this.switchSong(e,'next')}}
                        className='img_width2'
                        src={nextPlayButton}
                        alt=""
                    />
                </div>
                <div className='small_player_list'>
                    <img onClick={(e)=>this.playListDisplay(e)} className='img_width2_5' src={listButton} alt=""/>
                </div>
            </div>
        )
    }
}

export default SmallPlayer
