export const timeFormat = (time) => {
    if(isNaN(time)){
        return '00:00'
    }
    return `${Math.floor(time / 60)}:${parseInt(time) % 60 < 10 ? `0${parseInt(time) % 60}` : parseInt(time) % 60}`
};

export const yearFormat =(year)=>{
    if(!year){
        return null
    }
    let yearArr=year.split('-');
    return `${yearArr[0]}年${yearArr[1]}月${yearArr[2]}日`;
};
export const listenFormat=(listen)=>{
    if(listen>99999){
        return  parseInt((listen / 10000)) + '万'
    }else{
        return listen
    }
}
