import React,{Component} from 'react';
import ReactDOM from 'react-dom'
import './Message.styl';
import {connect} from "react-redux";
import {closeMessageAction} from '../../../action/songPlayerAction';
import MessageChildren from './MessageChildren';
class Message extends Component{
    message=(playerStore)=>{
        if(playerStore.errorMessage){
            return (
                <div className='message_container'>
                    <MessageChildren
                        closeMessageAction={this.props.closeMessageAction}
                        playerStore={this.props.playerStore}
                        />
                </div>
            )
        }else{
            return <div></div>
        }
};
    render() {
        return ReactDOM.createPortal(this.message(this.props.playerStore),document.getElementById('message'))
    }
}

const mapStateToProps=(state)=>{
    return {
        playerStore:state.playerStore
    }
}
export default connect(mapStateToProps,{closeMessageAction})(Message)
