import React, {Component, Fragment} from 'react';
import './ProgressBar.styl';
import {timeFormat} from '../../../common/js/format';
import PropTypes from 'prop-types';

//引入组件
class ProgressBar extends Component {
    static propTypes = {
        currentTime: PropTypes.number.isRequired,
        setAudioCurrentTime: PropTypes.func.isRequired,
        playerStoreLargePlayer:PropTypes.bool.isRequired
    };

    constructor(props) {
        super(props);
        this.state = {
            userTouch: false,
            progressBaringWidth: 0,
            pointStartX: 0,
            pointEndX: 0,
        }
    }
    componentWillReceiveProps(nextProps, nextContext) {
        if (!this.state.userTouch) {
                let width = this.refs.progress_bar.clientWidth / nextProps.songTime;
                this.progressBaringLength(width * nextProps.currentTime)
        }
    }

    progressBaringLength = (length) => {
        this.refs.progress_baring.style.width = length-1 + 'px';
        this.refs.progress_bar_point.style.transform = `translateX(${length + 'px'})`;
    };
    progressBarTouchStart = (e) => {
        this.setState({
            userTouch: true,
            progressBaringWidth: this.refs.progress_baring.clientWidth,
            pointStartX: e.touches[0].pageX,
        })
    };
    progressBarTouchMove = (e) => {
        this.setState({
            pointEndX: e.touches[0].pageX
        });
        let length = Math.max(0, Math.min(this.refs.progress_bar.clientWidth, this.state.pointEndX - this.state.pointStartX + this.state.progressBaringWidth))
        this.progressBaringLength(length);
    };
    progressBarTouchEnd = () => {
        let currentTime = this.props.songTime * (this.refs.progress_baring.clientWidth / this.refs.progress_bar.clientWidth)
        this.props.setAudioCurrentTime(currentTime);
        this.setState({
            userTouch: false
        })
    };

    render() {
        return (
            <Fragment>
                <div className='progress_bar_time'>{timeFormat(this.props.currentTime)}</div>
                <div ref='progress_bar' className='progress_bar'>
                    <div
                        ref='progress_bar_point'
                        onTouchStart={this.progressBarTouchStart}
                        onTouchMove={this.progressBarTouchMove}
                        onTouchEnd={this.progressBarTouchEnd}
                        className='progress_bar_point'
                    />
                    <div ref='progress_baring' className='progress_baring'/>
                </div>
                <div className='progress_bar_time'>{timeFormat(this.props.songTime)}</div>
            </Fragment>
        )
    }
}

export default ProgressBar
