import React ,{Component}from 'react';
import './Header.styl';
import logo from '../../../common/image/logo.svg'
class Header extends Component{
    render(){
        return (
            <div key='homeHeader' className='home_header'>
                <img className='home_header_logo' src={logo} alt="logo"/>
                <span className='header_text'>React Music</span>
            </div>
        )
    }
}

export default Header
