import React, {Component} from 'react';
import './PlayList.styl';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import Scroll from '../../common/Scroll/Scroll';
import deleteLogo from '../../../common/image/delete.svg';
import playLogo from '../../../common/image/play.svg';
import stopLogo from '../../../common/image/stop.svg';
import triangleLogo from '../../../common/image/triangle.svg';

//引入组件
class PlayList extends Component {
    static propTypes = {
        playerStore: PropTypes.object.isRequired,
        playListPlaySelectSongAction: PropTypes.func.isRequired,
        playListDeleteSongAction: PropTypes.func.isRequired,
    };
    playList = (playerStore) => {
        return playerStore.songList.map(list => {
            return (
                <li key={list.songMid} className='play_list_item'>
                    <span>
                        {
                            playerStore.songList[playerStore.currentPlaySong].songMid === list.songMid ?
                                <img className='play_list_triangle' src={triangleLogo} alt=""/> :
                                <span style={{width: '2rem'}}/>
                        }
                        <div className='play_list_song_name'>{list.songName}</div>
                    </span>
                    <span>{list.singerName}</span>
                    <span>{list.interval}</span>
                    <span>
                        {playerStore.songList[playerStore.currentPlaySong].songMid === list.songMid && playerStore.playing ?
                            <img
                                ref='playLogoTap'
                                onClick={() => {
                                    this.props.playListPlaySelectSongAction(playerStore.songList, playerStore.currentPlaySong, list.songMid);
                                    this.props.playChangeAction('pause');
                                }}
                                className='play_list_add'
                                src={stopLogo}
                                alt=""
                            />
                            :
                            <img
                                ref='playLogoTap'
                                onClick={() => {
                                    this.props.playListPlaySelectSongAction(playerStore.songList, playerStore.currentPlaySong, list.songMid);
                                    this.props.playChangeAction('play');
                                }}
                                className='play_list_add'
                                src={playLogo}
                                alt=""
                            />
                        }
                        <img
                            onClick={() => this.props.playListDeleteSongAction(playerStore.songList, playerStore.currentPlaySong, list.songMid)}
                            className='play_list_delete'
                            src={deleteLogo}
                            alt=""
                        />
                    </span>
                </li>
            )
        })
    };
    playListDisplay = () => {
        this.props.playListDisplayAction()
    }
    componentDidMount() {
        this.refs.playList.style.visibility='hidden'
        this.refs.playList.style.zIndex='-1'
        setTimeout(()=>{
            this.refs.playList.style.visibility='visible'
            this.refs.playList.style.zIndex='5'
        },500)
    }
    render() {
        const {playerStore} = this.props;
        return (
            <div
                ref='playList'
                className={classnames('play_list', {'slideInDown animated': !playerStore.playListDisplay}, {'slideInUp animated': playerStore.playListDisplay})}>
                <div className='play_list_title'>播放列表</div>
                <span onClick={() => this.playListDisplay()} className='play_list_close'>X</span>
                <div className='play_list_total'>总共{playerStore.songList.length}首歌</div>
                <Scroll top={'4rem'} scrollName='playListScroll'>
                    <ul>
                        {this.playList(playerStore)}
                    </ul>
                </Scroll>
            </div>
        )
    }
}

export default PlayList
