import Jsonp from 'jsonp';

export default (url,data,options)=>{
    let myUrl=urlhandler(url,data);
    myUrl=myUrl.slice(0,myUrl.length-1)
    return new Promise((resolve,reject)=>{
        Jsonp(myUrl,options,(err,data)=>{
            if(!err){
                resolve(data)
            }else{
                reject(err)
            }
        })
    })
}

const urlhandler=(url,data)=>{
    for(let k in data){
        if(Object.prototype.toString.call(data[k])==='[object Object]'){
            data[k]=JSON.stringify(data[k])
        }
        let value=encodeURIComponent(data[k])
            url+=`${k}=${value}`
            url+='&'
    }
    return url
};
