const initState = {
    list:[],
    flag: false
};
const singerDetailsStore = (state = initState, action = {}) => {
    switch (action.type) {
        case 'singerDetailsEmpty':
            return initState;
        case 'singerDetails':
            return {
                ...action.data,
                list:[...state.list,...action.data.list],
                flag: true
            };
        default:
            return state
    }
};

export default singerDetailsStore
