import React, {Fragment} from 'react';
import {Route, BrowserRouter as Router} from 'react-router-dom';
import Loadable from 'react-loadable';
import Home from '../components/Home/Home';
import Container from '../Container';
import Loading from '../components/common/Loading/Loading';
import Player from "../components/Player/Player";
//import {TransitionGroup, CSSTransition} from "react-transition-group";
import { AnimatedSwitch } from 'react-router-transition';
import './route.styl'
//import routeAnimate from '../components/common/routeAnimate/routeAnimate'
//import Singer from '../components/Singer/Singer';
//import Rank from '../components/Rank/Rank';
//import Recommend from '../components/Recommend/Recommend';
//import Search from '../components/Search/Search';
//import SingerDetails from "../components/SingerDetails/SingerDetails";
//import RankDetails from '../components/RankDetails/RankDetails';
const Hot =Loadable({
    loader:()=>import('../components/Hot/Hot'),
    loading:()=><Loading/>
})
const Singer = Loadable({
    loader: () => import('../components/Singer/Singer'),
    loading: () => <Loading/>,
});
const Rank = Loadable({
    loader: () => import('../components/Rank/Rank'),
    loading: () => <Loading/>,
});
const Recommend = Loadable({
    loader: () => import('../components/Recommend/Recommend'),
    loading: () => <Loading/>,
});
const Search = Loadable({
    loader: () => import('../components/Search/Search'),
    loading: () => <Loading/>,
});
const SingerDetails1 = Loadable({
    loader: () => import('../components/SingerDetails/SingerDetails1'),
    loading: () => <Loading/>,
});
const RankDetails = Loadable({
    loader: () => import('../components/RankDetails/RankDetails'),
    loading: () => <Loading/>,
});
export default (
    <Router>
        <Fragment>
            <Container>
                <AnimatedSwitch
                    atEnter={{ opacity: 0 }}
                    atLeave={{ opacity: 0 }}
                    atActive={{ opacity: 1 }}
                    className="fade"
                >
                    <Route exact path='/SingerDetails' component={SingerDetails1}/>
                    <Route exact path='/RankDetails' component={RankDetails}/>
                    <Route exact path='/Hot' component={Hot}/>
                    <Route path='/' render={() => {
                        return (
                            <Home>
                                <Route exact path='/Recommend' component={Recommend}/>
                                <Route exact path='/Singer' component={Singer}/>
                                <Route exact path='/Rank' component={Rank}/>
                                <Route exact path='/Search' component={Search}/>
                            </Home>
                        )
                    }}/>
                    <Route render={()=>{
                        return <div style={{color:"#fff",fontSize:'30px',textAlign:'center'}}>未找到页面</div>
                    }}/>
                </AnimatedSwitch>
            </Container>
            <Player/>
        </Fragment>
    </Router>
)


