import React, {Component} from 'react'
import IScroll from "iscroll/build/iscroll-probe";
import './Scroll.styl';

export default class Scroll extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {
        this.setState({
            [this.props.scrollName]: new IScroll(this.refs[this.props.scrollName], {
                probeType: 3,
                mouseWheel: true,
                bounce: true,
                scrollbars: false,
                click: true,
            })
        }, () => {
            if (this.props.onRankDetailsScroll) {
                this.state[this.props.scrollName].on('scroll', this.onRankDetailsScroll)
            }
            if (this.props.onSingerDetailsScroll) {
                this.state[this.props.scrollName].on('scroll', this.onSingerDetailsScroll)
            }
            if (this.props.dropDownRefresh) {
                this.state[this.props.scrollName].on('scroll', this.dropDownRefresh)
            }
            if (this.props.onHotScroll) {
                this.state[this.props.scrollName].on('scroll', this.onHotScroll)
            }
        })
    }
    componentWillUnmount() {
        this.state[this.props.scrollName].destroy();
    }

    componentDidUpdate(nextProps, P) {
        this.state[nextProps.scrollName].refresh();
        setTimeout(() => {
            this.state[nextProps.scrollName].refresh();
        }, 300)
    }

    dropDownRefresh = () => {

        if (this.props.songlength >= this.props.totalSong || this.props.currentPage >= 20) {
            return
        }
        let height = this.props.songlength * 62;
        let scrollHeight = this.refs.searchListScroll.scrollHeight;
        let transform = this.refs.scrollWraper.style.transform;
        let transformY = Number(transform.slice(15, transform.length - 19));
        if (scrollHeight - transformY > height + 30) {
            this.props.hotKeySearchAction(this.props.keyWord, this.props.currentPage + 1)
        }
    };
    onSingerDetailsScroll = () => {
        let height = this.props.songlength * 52;
        let scrollHeight = this.refs.singerDetailsScroll.scrollHeight;
        let transform = this.refs.scrollWraper.style.transform;
        let transformY = Number(transform.slice(15, transform.length - 19));

        if (scrollHeight - transformY > height + 100 && this.props.songlength < this.props.totalSong) {
            this.props.singerDetailsAction(this.props.singerMid, this.props.songlength)
        }
    };
    onHotScroll = () => {
        if (!this.refs.scrollWraper) {
            return
        }
        let transform = this.refs.scrollWraper.style.transform;
        let transformY = transform.slice(15, transform.length - 19);
        let top = this.props.hotButton.offsetTop;
        if (transformY <= -top) {
            transformY = -top
        }
        this.props.hotTop.style.transform = `translate3d(0,${transformY}px,0)`;
    }
    onRankDetailsScroll = () => {
        if (!this.refs.scrollWraper) {
            return
        }
        let transform = this.refs.scrollWraper.style.transform;
        let transformY = transform.slice(15, transform.length - 19);
        let top = this.props.rankDetailsButton.offsetTop;
        if (transformY <= -top) {
            transformY = -top
        }
        this.props.rankDetailsTop.style.transform = `translate3d(0,${transformY}px,0)`;
    };

    render() {
        return (
            <div className='scroll_container'
                 style={{top: this.props.top, bottom: this.props.bottom}}
                 ref={this.props.scrollName}
            >
                <div className='scroll' ref='scrollWraper' style={{paddingTop: this.props.paddingTop}}>
                    {this.props.children}
                </div>
            </div>
        )
    }
}
