import React, {Component} from 'react';
import classnames from "classnames";
import Progress from "../ProgressBar/ProgressBar";
import Lyric from "../Lyric/Lyric";
import './LargePlayer.styl';
import PropTypes from 'prop-types';
/*svg*/
import backButton from "../../../common/image/back.svg";
import orderPlayButton from "../../../common/image/orderPlay.svg";
import randomPlayButton from "../../../common/image/randomPlay.svg";
import prePlayButton from "../../../common/image/prePlay.svg";
import stopButton from "../../../common/image/stop.svg";
import playButton from "../../../common/image/play.svg";
import nextPlayButton from "../../../common/image/nextPlay.svg";
import emptyButton from "../../../common/image/empty.svg";
import listButton from "../../../common/image/list.svg";
import musicLogo from "../../../common/image/music.svg";

class LargePlayer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imgSrc: musicLogo,
            imgRotate:'0deg'
        }
    }

    static propTypes = {
        orderChangeAction: PropTypes.func.isRequired,
        playChangeAction: PropTypes.func.isRequired,
        playerStore: PropTypes.object.isRequired,
        largePlayerToSmallPlayerAction: PropTypes.func.isRequired,
        switchSong: PropTypes.func.isRequired,
        songListEmptyAction: PropTypes.func.isRequired,
        playListDisplayAction: PropTypes.func.isRequired,
        currentTime: PropTypes.number.isRequired,
        songTime: PropTypes.number.isRequired,
        setAudioCurrentTime: PropTypes.func.isRequired,
    };

  /*  componentWillReceiveProps(nextProps, nextContext) {
        console.log(nextProps.playerStore.playing);
        if(nextProps.playerStore.playing){
            //this.refs.largePlayerContainerImg.children[0]
            console.log(this.refs.largePlayerContainerImg.children[0].getAttribute('rotate_animation'));
            this.setState({
                imgRotate:'0deg'
            })
        }
    }*/

    orderChange = (e, data) => {
        e.preventDefault();
        this.props.orderChangeAction(data)
    };
    playChange = (e, data) => {
        e.preventDefault();
        this.props.playChangeAction(data)
    };
    switchSong = (data) => {
        this.props.switchSong(data)
    };
    imgOnload = (e,picUrl) => {
        e.stopPropagation();
        this.setState({
            imgSrc: picUrl
        })
    }
    render() {
        const {playerStore} = this.props;
        return (
            <div className={classnames('large_player_container', {'player_display': !playerStore.largePlayer})}>
                <div className='large_player_container_header'>
                    <img
                        onClick={this.props.largePlayerToSmallPlayerAction}
                        className='img_width3'
                        src={backButton}
                        alt=""/>
                    <div className='large_player_songName'>
                        {
                            playerStore.songList.length === 0 ?
                                'ReactMusic'
                                :
                                playerStore.songList[playerStore.currentPlaySong].songName
                        }
                    </div>
                </div>
                <div ref='largePlayerContainerImg' className={classnames('large_player_container_img',"rotate_animation",{'rotate_animation_pause':!playerStore.playing})}>
                    {
                        playerStore.songList.length=== 0?
                            <img src={musicLogo} alt=""/>
                            :
                            <img  src={this.state.imgSrc} alt=""/>
                    }
                    <img style={{display:'none'}} onLoad={(e)=>this.imgOnload(e,playerStore.songList[playerStore.currentPlaySong].picUrl)} src={playerStore.songList[playerStore.currentPlaySong].picUrl} alt=""/>
                </div>
                <div className='large_player_container_lyric'>
                    <Lyric
                        Lyric={playerStore.songList.length === 0 ? '' : playerStore.songList[playerStore.currentPlaySong].lyric}
                        currentTime={this.props.currentTime}
                    />
                </div>
                <div className='large_player_container_progress_bar'>
                    {
                        playerStore.songList.length === 0 ? null : <Progress
                            playerStoreLargePlayer={playerStore.largePlayer}
                            currentTime={this.props.currentTime}
                            songTime={this.props.songTime}
                            setAudioCurrentTime={this.props.setAudioCurrentTime}
                        />
                    }
                </div>
                <div className='large_player_container_tools'>
                    <div className='player_order'>
                        {playerStore.order ?
                            <img
                                onClick={(e) => this.orderChange(e, 'unorder')}
                                className='img_width1_5'
                                src={randomPlayButton}
                                alt=""
                            />
                            :
                            <img
                                onClick={(e) => this.orderChange(e, 'order')}
                                className='img_width1_5'
                                src={orderPlayButton}
                                alt=""
                            />
                        }
                    </div>
                    <div className='player_select'>
                        <img
                            onClick={() => this.switchSong('pre')}
                            className='img_width2'
                            src={prePlayButton}
                            alt=""
                        />
                        {playerStore.playing ?
                            <img
                                onClick={(e) => this.playChange(e, 'pause')}
                                className='img_width3'
                                src={stopButton}
                                alt=''
                            />
                            :
                            <img
                                onClick={(e) => this.playChange(e, 'play')}
                                className='img_width3'
                                src={playButton}
                                alt=""
                            />
                        }
                        <img
                            onClick={() => this.switchSong('next')}
                            className='img_width2'
                            src={nextPlayButton}
                            alt=""
                        />
                        <img onClick={this.props.songListEmptyAction} className='img_width1_5' src={emptyButton}
                             alt=''/>
                    </div>
                    <div className='player_song_list'>
                        <img onClick={this.props.playListDisplayAction} className='img_width2' src={listButton} alt=""/>
                    </div>
                </div>
            </div>
        )
    }
}

export default LargePlayer
