import React, {Component} from 'react';
import './Lyric.styl';
import {lyricReg} from "../../../common/js/lyricReg";
import PropTypes from 'prop-types';
//引入组件
class Lyric extends Component {
    static propTypes={
        Lyric:PropTypes.string.isRequired,
        currentTime:PropTypes.number.isRequired
    };
    lyricDisplay=(lyric)=>{
        if(!lyric){
            return {
                preLyric:null,
                currentLyric:'暂无歌词',
                nextLyric:null
            }
        }
        for(let i=0;i<lyric.lyrictimeArr.length;i++){
            if(this.props.currentTime>lyric.lyrictimeArr[lyric.lyrictimeArr.length-1]){
                return {
                    preLyric:lyric.lyricBodyArr[lyric.lyrictimeArr.length-2],
                    currentLyric:lyric.lyricBodyArr[lyric.lyrictimeArr.length-1]
                }
            }
            if(i===lyric.lyrictimeArr.length-1){
                return lyric.lyricBodyArr[lyric.lyrictimeArr.length-1];
            }
            if(this.props.currentTime>=lyric.lyrictimeArr[i]&&this.props.currentTime<=lyric.lyrictimeArr[i+1]){
                if(i===0){
                    return {
                        currentLyric:lyric.lyricBodyArr[i],
                        nextLyric:lyric.lyricBodyArr[i+1]
                    }
                }else{
                    return {
                        preLyric:lyric.lyricBodyArr[i-1],
                        currentLyric:lyric.lyricBodyArr[i],
                        nextLyric:lyric.lyricBodyArr[i+1]
                    }
                }
            }
        }
    };
    render() {
        let lyric=lyricReg(this.props.Lyric);
        return (
            <div className='player_lyric'>
                <div className='player_lyric_color'>
                    {this.lyricDisplay(lyric).preLyric}
                </div>
                <div>
                    {this.lyricDisplay(lyric).currentLyric}
                </div>
                <div className='player_lyric_color'>
                    {this.lyricDisplay(lyric).nextLyric}
                </div>
            </div>
        )
    }
}

export default Lyric
