let songMidCache={};
let FailCache=[];
export const songCache=(songMid)=>{
    for(let k in songMidCache){
        if(k===songMid){
            return false
        }
    }
    songMidCache[songMid]='';
    return true
};

export const songCacheClear=(songMid)=>{
    for(let k in songMidCache){
        if(k===songMid){
            delete songMidCache[k];
        }
    }
};

export const add$Cache=(songMid)=>{
    FailCache.push(songMid)
}

export const $Cache=(songMid)=>{
    return FailCache.indexOf(songMid)>-1?true:false
}
