import React,{Component} from 'react';
import './Message.styl';
import messageLogo from '../../../common/image/message.svg';
class MessageChildren extends Component{
    componentWillMount(){
        setTimeout(()=>this.props.closeMessageAction(),3000)
    }
    render() {
        return(
            <div className='message_info_container'>
                <img src={messageLogo} alt=""/>
                <div>{this.props.playerStore.content}</div>
            </div>
        )
    }
}

export default MessageChildren
