import React, {Component} from 'react';
import './Nav.styl';
import {NavLink} from 'react-router-dom';
import classNames from 'classnames'
import {withRouter} from 'react-router-dom'
//引入组件
class Nav extends Component {
    state = {
        '/Recommend': '首页',
        '/Singer': '歌手',
        '/Rank': '排行',
        '/Search': '搜索'
    }

    navList = () => {
        let navArr = []
        for (let k in this.state) {
            navArr.push(
                <li key={k} className={classNames({nav_item:true,
                    select:this.props.history.location.pathname===k?true:false
                })}>
                    <NavLink className='nav_item_link' to={k}>
                        {this.state[k]}
                    </NavLink>
                </li>
            )
        }
        return (
            <ul key='nav' className='nav'>
                {navArr}
            </ul>
        )
    }

    render() {
        return this.navList()
    }
}

export default withRouter(Nav)
