export const lyricReg = (lyricsText) => {
    if(!lyricsText){
        return null
    }
    if(lyricsText=='[00:00:00]此歌曲为没有填词的纯音乐，请您欣赏'){
        return null
    }
    let lyric=lyricsText.split('[offset:0]');
    let pattern = /\[\d{2}:\d{2}\.\d{2}]/g;
    if(!lyric[1]){
        return null
    }
    let lyricBody=lyric[1].split(pattern);
    let lyricTime =lyric[1].match(pattern);
    lyricBody.shift();
    let lyricemptyreg=/^\s/g;
    let lyricBodyArr=[];
    let lyrictimeArr=[];
    for(let i=0;i<lyricBody.length;i++){
        if(!lyricBody[i].match(lyricemptyreg)){
            lyricBodyArr.push(lyricBody[i]);
            lyrictimeArr.push(lyricTime[i].slice(1,lyricTime[i].length-4))
        }
    }
    let newlyrictimeArr=[];
    for(let i=0;i<lyrictimeArr.length;i++){
        let time=lyrictimeArr[i].split(':');
        newlyrictimeArr.push(Number(time[0])*60+Number(time[1]));
    }

    return {
        lyricBodyArr,
        lyrictimeArr:newlyrictimeArr
    }
};
