const initState = {
    songList: [],
    largePlayer: false,
    smallPlayer: false,
    playListDisplay: false,
    playing: false,
    order: false,
    flag: false,
    currentPlaySong: 0,
    errorMessage: false,
    content: ''
};

const playerStore = (state = initState, action = {}) => {
    switch (action.type) {
        case 'songPlay':
            return {
                ...initState,
                songList: [action.data, ...state.songList],
                flag: true,
                largePlayer: true,
                playing: true
            };
        case 'playAllSong':
            return {
                ...state,
                flag: true,
                songList: action.data,
                playing: true,
                largePlayer: true,
                smallPlayer: false,
                currentPlaySong: 0
            };
        case 'largePlayerToSmallPlayer':
            return {
                ...state,
                largePlayer: false,
                smallPlayer: true
            };
        case 'smallPlayerToLargePlayer':
            return {
                ...state,
                smallPlayer: false,
                largePlayer: true
            };
        case 'playChange':
            return {
                ...state,
                playing: action.data
            };
        case 'orderChange':
            return {
                ...state,
                order: action.data
            };
        case 'nextSong':
            return {
                ...state,
                currentPlaySong: action.data
            };
        case 'preSong':
            return {
                ...state,
                currentPlaySong: action.data
            };
        case 'songPlayEnd':
            return {
                ...state,
                currentPlaySong: action.data
            };
        case 'playListDisplay':
            return {
                ...state,
                playListDisplay: !state.playListDisplay
            };
        case 'songListEmpty':
            return {
                ...state,
                playing: false,
                flag: false,
                songList: []
            };
        case 'playListPlaySelectSong':
            return {
                ...state,
                currentPlaySong: action.data
            };
        case 'playListDeleteSong':
            return {
                ...state,
                songList: action.songList,
                playing: action.playing,
                currentPlaySong: action.currentPlaySong
            };
        case 'songAdd' :
            return {
                ...initState,
                songList: [...state.songList, action.data],
                flag: true,
                playing: action.playing,
                smallPlayer: true,
                currentPlaySong: action.currentPlaySong
            };
        case 'default':
            return state;
        case 'message':
            return {
                ...state,
                errorMessage: true,
                content: action.data
            }
        case 'closeMessage':
            return {
                ...state,
                errorMessage: false,
                content: ''
            }
        default:
            return state;
    }
};

export default playerStore

