const initState={
    songList:{},
    searchFlag:false,
    searchListFlag:false,
    searchListLoadingFlag:false,
    flag:false
};
const  searchStore= (state =initState, action = {}) => {
    switch (action.type) {
        case 'search':
            return {
                ...action.data,
                flag: true,
                searchFlag: false
            };
        case 'hotKeySearch':
            return {
                ...state,
                songList: {
                    ...action.data
                },
                searchListLoadingFlag:true
            };
        case 'hotKeySearchLoading':
            return{
                ...state,
                searchListFlag:true
            }
        case 'hotKeySearchRefersh':
            return{
                ...state,
                songList:{
                    ...state.songList,
                    data:{
                        ...state.songList.data,
                        song:{
                            ...state.songList.data.song,
                            list:[...state.songList.data.song.list,...action.list],
                            curpage:action.curpage
                        }
                    }
                }
            };
        case 'searchHistoryDisplay':
            return {
                ...state,
                searchFlag:true
            };
        case 'searchHistoryDisplayClose':
            return {
                ...state,
                searchFlag:false
            };
        case 'searchListClose':
            return {
                ...state,
                searchListFlag:false,
                searchListLoadingFlag:false,
                searchFlag:true
            };
        case 'error':
            return {
                ...state
            };
        default:
            return state;
    }
};

export default searchStore
