import React, {Component} from 'react';
import './Player.styl';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {
    songPlayAction,
    switchSongAction,
    largePlayerToSmallPlayerAction,
    smallPlayerToLargePlayerAction,
    playChangeAction,
    orderChangeAction,
    playListDisplayAction,
    songListEmptyAction,
    playListPlaySelectSongAction,
    playListDeleteSongAction,
    messageAction
} from '../../action/songPlayerAction';
import LargePlayer from "./LargePlayer/LargePlayer";
import SmallPlayer from "./SmallPlayer/SmallPlayer";
import PlayList from './PlayList/PlayList';
import Message from "../common/Message/Message";

//引入组件
class Player extends Component {
    static propTypes = {
        playerStore: PropTypes.object.isRequired,
        songPlayAction: PropTypes.func.isRequired,
        playChangeAction: PropTypes.func.isRequired,
        orderChangeAction: PropTypes.func.isRequired,
        largePlayerToSmallPlayerAction: PropTypes.func.isRequired,
        smallPlayerToLargePlayerAction: PropTypes.func.isRequired,
        switchSongAction: PropTypes.func.isRequired,
        playListDisplayAction: PropTypes.func.isRequired,
        songListEmptyAction: PropTypes.func.isRequired,
        playListPlaySelectSongAction: PropTypes.func.isRequired,
        playListDeleteSongAction: PropTypes.func.isRequired,
        messageAction: PropTypes.func.isRequired,
    };
    state = {
        currentTime: 0,
        songTime: 0
    };

    componentWillUpdate(nextProps) {
        if (nextProps.playerStore.songList.length !== 0) {
            if (nextProps.playerStore.playing) {
                setTimeout(() => {
                    if(!this.refs.player){
                        return
                    }
                    this.refs.player.play()
                }, 0)
            }
            else if (!nextProps.playerStore.playing) {
                setTimeout(() => {
                    if(!this.refs.player){
                        return
                    }
                    this.refs.player.pause()
                }, 0)
            }
        }
    }

    setAudioCurrentTime = (data) => {
        if (!isFinite(data)) {
            this.refs.player.currentTime = this.state.currentTime;
        } else {
            this.refs.player.currentTime = data;
        }
    };
    songTime = (e) => {

        this.setState({
            currentTime: e.target.currentTime,
            songTime: e.target.duration
        });
        if (this.state.currentTime === this.state.songTime && this.state.currentTime !== 0 && this.state.songTime !== 0 && !isNaN(this.state.songTime)) {
            this.switchSong('end')
        }
    };
    switchSong = (type) => {
        this.props.switchSongAction(
            this.props.playerStore.order,
            this.props.playerStore.currentPlaySong,
            this.props.playerStore.songList.length - 1,
            type
        )
    };
    validMusicUrl=(url)=>{
        if(!url){
            this.props.messageAction()
            return true
        }
        let urlArr=url.split('&vkey=')
        if(urlArr[1]===''||urlArr[1]==='songinfo size error'){
            this.props.messageAction()
            return true
        }else{
            return false
        }
    }
    playerDisplay = (playerStore) => {
        if (playerStore.flag) {
            if(playerStore.songList.length!==0&&this.validMusicUrl(playerStore.songList[playerStore.currentPlaySong].musicUrl)){
                this.switchSong('next')
            }
            return (
                <div className='player_container'>
                    <LargePlayer
                        playListDisplayAction={this.props.playListDisplayAction}
                        playerStore={playerStore}
                        playChangeAction={this.props.playChangeAction}
                        orderChangeAction={this.props.orderChangeAction}
                        largePlayerToSmallPlayerAction={this.props.largePlayerToSmallPlayerAction}
                        currentTime={this.state.currentTime}
                        songTime={this.state.songTime}
                        setAudioCurrentTime={this.setAudioCurrentTime}
                        switchSong={this.switchSong}
                        songListEmptyAction={this.props.songListEmptyAction}
                    />
                    <SmallPlayer
                        playListDisplayAction={this.props.playListDisplayAction}
                        playerStore={playerStore}
                        playChangeAction={this.props.playChangeAction}
                        smallPlayerToLargePlayerAction={this.props.smallPlayerToLargePlayerAction}
                        switchSong={this.switchSong}
                    />
                    <PlayList
                        playListPlaySelectSongAction={this.props.playListPlaySelectSongAction}
                        playListDisplayAction={this.props.playListDisplayAction}
                        playerStore={playerStore}
                        playListDeleteSongAction={this.props.playListDeleteSongAction}
                        playChangeAction={this.props.playChangeAction}
                    />
                    {
                        playerStore.songList.length === 0 ?
                            null :
                            <audio ref='player'
                                   onTimeUpdate={this.songTime}
                                   src={playerStore.songList[playerStore.currentPlaySong].musicUrl}
                            />
                    }
                    <Message/>
                </div>
            )
        } else {
            return null
        }
    };

    render() {
        const {playerStore} = this.props;
        return (
            this.playerDisplay(playerStore)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        playerStore: state.playerStore
    }
};
export default connect(
    mapStateToProps,
    {
        songPlayAction,
        playChangeAction,
        orderChangeAction,
        largePlayerToSmallPlayerAction,
        smallPlayerToLargePlayerAction,
        switchSongAction,
        playListDisplayAction,
        songListEmptyAction,
        playListPlaySelectSongAction,
        playListDeleteSongAction,
        messageAction
    }
)(Player);
