import {combineReducers} from 'redux';
import recommendStore from './recommendStore';
import singerStore from './singerStore';
import singerDetailsStore from './singerDetailStore';
import playerStore from './playerStore';
import scrollStore from './scrollStore';
import rankStore from './rankStore';
import rankDetailsStore from "./rankDetailsStore";
import searchStore from './searchStore';
import hotSongStore from "./hotSongStore";
const reducers =combineReducers({
    recommendStore,
    singerStore,
    singerDetailsStore,
    playerStore,
    scrollStore,
    rankStore,
    rankDetailsStore,
    searchStore,
    hotSongStore
});

export default reducers;
