const initState = {
    flag: false,
    songLoading: true
};
const hotSongStore = (state = initState, action = {}) => {
    switch (action.type) {
        case 'hotSong':
            return {
                ...state,
                songLoading: true,
                flag: true,
                data: action.data
            }
        case 'hotSongLoading':
            if (action.data[0].songlist.length < 15) {
                return {
                    ...state,
                    songLoading: false,
                    data: {
                        ...state.data,
                        tracks: [...state.data.tracks, ...action.data[0].songlist]
                    }
                }
            } else {
                return {
                    ...state,
                    data: {
                        ...state.data,
                        tracks: [...state.data.tracks, ...action.data[0].songlist]
                    }
                }
            }

        default:
            return state
    }
};

export default hotSongStore
