import Jsonp from '../common/js/myJsonp';
import axios from 'axios';
import {timeFormat} from '../common/js/format';
const url = 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?';
const commonOptions = {
    param: 'callback'
};
export const songPlayVkeyRequest = (songMid) => {
    const data = {
        loginUin: 822059927,
        format: 'jsonp',
        platform: 'yqq',
        needNewCode: 0,
        cid: 205361747,
        uin: 0,
        guid: 0,
        songmid: songMid,
        filename: `C400${songMid}.m4a`
    };
    return Jsonp(url, data, commonOptions)
};
export const songLyricRequest = (songMid) => {
    const value = {
        param: 'callback',
        pcachetime: +new Date(),
        songmid: songMid,
        g_tk: 1123594732,
        loginUin: 822059927,
        hostUin: 0,
        format: 'jsonp',
        inCharset: 'utf8',
        outCharset: 'utf-8',
        notice: 0,
        platform: 'yqq',
        needNewCode: 0
    };
    return axios.get('/api/lyric', {
        params: value
    })
};//http://192.168.43.64:8080

export const songPlayApi = (songMid, albumMid, interval, singerName,songName,songLyric, vkey) => {
    let musicUrl = `http://dl.stream.qqmusic.qq.com/C400${songMid}.m4a?fromtag=38&guid=0&vkey=${vkey}`;
    let picUrl = `http://y.gtimg.cn/music/photo_new/T002R300x300M000${albumMid}.jpg?max_age=2592000`;
    let Interval = timeFormat(interval);
    return {
        musicUrl,
        picUrl,
        interval: Interval,
        singerName: singerName,
        songName:songName,
        songMid:songMid,
        lyric:songLyric
    }
};

