const initState = {
    slider: [],
    radioList: [],
    songList: [],
    error: '',
    flag: false
};
const rankStore = (state = initState, action = {}) => {
    switch (action.type) {
        case 'rankList':
            return {
                ...action.data,
                flag: true
            };
        case 'rankError':
            return {error: '404'};
        default:
            return state
    }
};

export default rankStore
