import {songPlayApi, songPlayVkeyRequest, songLyricRequest} from '../api/songPlay';
import {songCache, songCacheClear, add$Cache, $Cache} from '../common/js/songCache';
import {Base64} from "js-base64";

const songRequestFail = (dispatch, songMid, vkey) => {
    if (vkey === '') {
        add$Cache(songMid)
        dispatch({type: 'message', data: '此歌曲是收费歌曲'})
        return true
    } else {
        return false
    }
};

export const songPlayAction = (songList, songMid, albumMid, interval, singerName, songName) => {
    if (!songCache(songMid)) {
        return dispatch => dispatch({type: 'default'})
    }
    if ($Cache(songMid)) {
        return dispatch => dispatch({type: 'message', data: '此歌曲是付费歌曲'})
    }
    for (let k in songList) {
        if (songList[k].songMid === songMid) {
            return dispatch => dispatch({type: 'default'})
            //全局对话框，已经添加此歌曲
        }
    }
    return dispatch => {
        songPlayVkeyRequest(songMid).then(res => {
            if (res.code === 0) {
                if (!songRequestFail(dispatch, songMid, res.data.items[0].vkey)) {
                    songLyricRequest(songMid).then(lyricRes => {
                        if(res.data.items[0].vkey==="songinfo size error"){
                          return dispatch({type: 'message', data: '此歌曲是付费歌曲'})
                        } else {if (lyricRes.data.code === 0) {
                            let newlyric = Base64.decode(lyricRes.data.lyric);
                            dispatch({
                                type: 'songPlay',
                                data: songPlayApi(songMid, albumMid, interval, singerName, songName, newlyric, res.data.items[0].vkey)
                            })
                        } else {
                            dispatch({
                                type: 'songPlay',
                                data: songPlayApi(songMid, albumMid, interval, singerName, songName, '', res.data.items[0].vkey)
                            })
                        }
                    }})
                }
            }
        })
    }
};



