export const playAllSongAction = (songList, type, media) => {
    let songPlayVkeyPromiseArr = [];
    let songLyricPromiseArr = [];
    for (let i = 0; i < songList.length; i++) {
        let songMid = ''
        if (media) {
            songMid=songList[i].file.media_mid;
        } else {
            songMid = songList[i][type].songmid;
        }
        songPlayVkeyPromiseArr.push(songPlayVkeyRequest(songMid));
        songLyricPromiseArr.push(songLyricRequest(songMid));
    }

    return dispatch => Promise.all(songPlayVkeyPromiseArr).then(VkeyRes => {
        Promise.all(songLyricPromiseArr).then(lyricRes => {
            let allSongList = [];
            for (let i = 0; i < lyricRes.length; i++) {
                if(type){
                    let songMid = songList[i][type].songmid;
                    let newlyric = Base64.decode(lyricRes[i].data.lyric);
                    allSongList.push(songPlayApi(
                        songMid,
                        songList[i][type].albummid,
                        songList[i][type].interval,
                        songList[i][type].singer[0].name,
                        songList[i][type].songname,
                        newlyric,
                        VkeyRes[i].data.items[0].vkey
                    ))
                }else{
                    let newlyric=''
                    let songMid = songList[i].file.media_mid;
                    if(lyricRes[i].data.code!==-1901){
                        newlyric = Base64.decode(lyricRes[i].data.lyric);
                    }
                    allSongList.push(songPlayApi(
                        songMid,
                        songList[i].album.mid,
                        songList[i].interval,
                        songList[i].singer[0].name,
                        songList[i].name,
                        newlyric,
                        VkeyRes[i].data.items[0].vkey
                    ))
                }

            }
            dispatch({
                type: 'playAllSong',
                data: allSongList
            })
        })
    })
};

export const songAddAction = (songList, currentPlaySong, playing, songMid, albumMid, interval, singerName, songName) => {
    if (!songCache(songMid)) {
        return dispatch => dispatch({type: 'default'})
    }
    if ($Cache(songMid)) {
        return dispatch => dispatch({type: 'message', data: '此歌曲是付费歌曲'})
    }
    for (let k in songList) {
        if (songList[k].songMid === songMid) {
            return dispatch => dispatch({type: 'default'})
            //全局对话框，已经添加此歌曲
        }
    }
    if (songList.length === 0) {
        playing = true
    }
    return dispatch => {
        songPlayVkeyRequest(songMid).then(res => {
            if (res.code === 0) {
                if (!songRequestFail(dispatch, songMid, res.data.items[0].vkey)) {
                    songLyricRequest(songMid).then(lyricRes => {
                        if (lyricRes.data.code === 0) {
                            let newlyric = Base64.decode(lyricRes.data.lyric);
                            dispatch({
                                type: 'songAdd',
                                data: songPlayApi(songMid, albumMid, interval, singerName, songName, newlyric, res.data.items[0].vkey),
                                currentPlaySong: currentPlaySong,
                                playing: playing
                            })
                        } else {
                            dispatch({
                                type: 'songAdd',
                                data: songPlayApi(songMid, albumMid, interval, singerName, songName, '', res.data.items[0].vkey),
                                currentPlaySong: currentPlaySong,
                                playing: playing
                            })
                        }
                    })
                }
            }
        })
    }
};
