export const playChangeAction = (data) => {
    if (data === 'pause') {
        return dispatch => dispatch({type: 'playChange', data: false})
    } else {
        return dispatch => dispatch({type: 'playChange', data: true})
    }

};

export const orderChangeAction = (data) => {
    if (data === 'order') {
        return dispatch => dispatch({type: 'orderChange', data: true})
    } else {
        return dispatch => dispatch({type: 'orderChange', data: false})
    }

};

export const switchSongAction = (orderState, currentPlaySong, songTotal, type) => {
    if (!orderState) {
        if (type === 'next' || type === 'end') {
            let currentPlaySongIndex = currentPlaySong;
            if (currentPlaySongIndex === songTotal) {
                currentPlaySongIndex = 0;
            } else {
                currentPlaySongIndex = currentPlaySongIndex + 1;
            }
            return dispatch => dispatch({type: 'nextSong', data: currentPlaySongIndex})
        } else {
            let currentPlaySongIndex = currentPlaySong;
            if (currentPlaySongIndex === 0) {
                currentPlaySongIndex = songTotal
            } else {
                currentPlaySongIndex = currentPlaySongIndex - 1
            }
            return dispatch => dispatch({type: 'preSong', data: currentPlaySongIndex})
        }
    } else {
        let currentPlaySongIdex = Math.floor(Math.random() * (songTotal + 1));

        while (currentPlaySongIdex === currentPlaySong) {
            currentPlaySongIdex = Math.floor(Math.random() * (songTotal + 1));
        }
        return dispatch => dispatch({type: 'nextSong', data: currentPlaySongIdex})
    }
};

