export const playListPlaySelectSongAction = (songList, currentPlaySong, songMid) => {
    for (let k in songList) {
        if (songList[k].songMid === songMid) {
            if (k === currentPlaySong) {
                return dispatch => dispatch({type: 'default'})
            } else {
                return dispatch => dispatch({type: 'playListPlaySelectSong', data: Number(k)})
            }
        }
    }
};

export const playListDeleteSongAction = (songList, currentPlaySong, songMid) => {
    songCacheClear(songMid);
    if (songList.length === 1) {
        return dispatch => dispatch({
            type: 'playListDeleteSong',
            songList: [],
            playing: false,
            currentPlaySong: 0
        })
    } else {
        for (let k in songList) {
            if (songList[k].songMid === songMid) {//找到歌曲序列号
                let index = Number(k);
                let newSongList = [...songList.slice(0, index), ...songList.slice(index + 1, songList.length)]
                if (index === currentPlaySong && (index === songList.length - 1 || index === 0)) {
                    //判断歌曲是否在播放//判断歌曲是否在最后一个和第一个
                    return dispatch => dispatch({
                        type: 'playListDeleteSong',
                        songList: newSongList,
                        playing: true,
                        currentPlaySong: 0
                    })
                } else if (index >= currentPlaySong) {
                    return dispatch => dispatch({
                        type: 'playListDeleteSong',
                        songList: newSongList,
                        playing: true,
                        currentPlaySong: Number(currentPlaySong),
                    })
                } else if (index < currentPlaySong) {
                    songList.splice(index, index + 1);
                    return dispatch => dispatch({
                        type: 'playListDeleteSong',
                        songList: newSongList,
                        playing: true,
                        currentPlaySong: Number(currentPlaySong) - 1,
                    })
                }
            }
        }
    }
};

